@import url("https://fonts.googleapis.com/css2?family=Trocchi&display=swap");
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@500&family=JetBrains+Mono:wght@500&display=swap");

body {
	margin: 0;
	height: 100%;
}

html {
	height: 100%;
}

#root {
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;

	--main-font: "Trocchi", serif;
	--mono-font: "JetBrains Mono", monospace;

  --primary-color1:rgb(156, 67, 58);
  --primary-color2:rgb(98, 156, 58);
  --primary-color3:rgb(116, 58, 156);
  --primary-color4:rgb(58, 147, 156);
  --primary-color5:rgb(179, 168, 72);

	--bg-color: rgba(173, 167, 144, 0.7);
	--textbox-bg: rgb(225, 225, 225);
	--g-button-bg: lightgrey;
	--g-button-hover-bg: khaki;
	--clear-click-bg: greenyellow;

	--discourse-display-bg: lightgray;

	--discourse-1-title-bg: var(--primary-color1);
	--discourse-2-title-bg: var(--primary-color2);
	--discourse-3-title-bg: var(--primary-color3);
	--discourse-4-title-bg: var(--primary-color4);
	--outer-ring-title-bg: var(--primary-color5);

  --agent: var(--primary-color1);
  --other: var(--primary-color2);
  --product: var(--primary-color3);
  --truth: var(--primary-color4);
	--matheme-option-bg: rgba(211, 211, 211, 0.8); /* this is lightgrey btw */

	--nametable-bg: rgba(138, 125, 125, 0.95);
	--nametable-header-bg: rgba(255, 99, 71, 0.336);
	--new-discourse-option-bg: rgba(211, 211, 211, 0.8);

	--info-box-outer-bg: rgb(211, 211, 211);
	--info-box-inner-bg: rgb(225, 225, 225);

	/* --overview-title-bg: rgba(139, 0, 139, 0.5); */
	--overview-title-bg: rgb(174, 103, 91);
	--ring-even-bg: darkgrey;
	--ring-odd-bg: grey;
	--ring-title-text: black;
	--ring-title-bg: var(--outer-ring-title-bg);
	--ring-title-even-bg: rgb(174, 134, 207);
	--in-ring-name-text: black;

	--blaze-color: chartreuse;
	--blaze-shadow: tomato;
	--four20-color: tomato;
	--four20-shadow: purple;

	font-family: var(--main-font);
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.image-container img {
	max-height: 100%;
	width: auto;
}
