.overview {
	grid-column-start: 1;
	grid-column-end: 4;
	grid-row-start: 5;
	grid-row-end: 8;
	justify-self: center;
	align-self: center;

	width: 32vw;
	height: 80%;

	margin: 0;
	/* resize: both;
  overflow: auto; */
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: auto 1fr 1fr;

	z-index: 1501;
	border: solid black 1px;

	opacity: 0.95;
}

.overview-title {
	position: relative;
	grid-column-start: 1;
	grid-column-end: 4;
	grid-row-start: 1;
	grid-row-end: 2;

	border: solid black 1px;
	background: var(--overview-title-bg);
	text-align: center;

	cursor: grab;
}

.overview-title:active {
	cursor: grabbing;
}

.close-overview-button {
position: absolute;
right: 0;
top: 0;

}