.stalin {
	border-radius: 50%;
	max-height: 100%;
	max-width: 100%;
}

.stalin:hover {
	cursor: crosshair;
	-webkit-animation-name: blazeit;
	        animation-name: blazeit;
	-webkit-animation-duration: 1000ms;
	        animation-duration: 1000ms;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-timing-function: linear;
	        animation-timing-function: linear;
	box-shadow: 0 0 10px #5eff00dc, 0 0 20px #308300dc, 0 0 30px #71ce08e1,
		0 0 40px #71ce08e1, 0 0 50px #71ce08e1, 0 0 60px #71ce08e1,
		0 0 70px #71ce08e1;
}

@-webkit-keyframes blazeit {
	0% {
		-webkit-transform: scale(1, 1) rotate(0deg);
		        transform: scale(1, 1) rotate(0deg);
	}
	12.5% {
		-webkit-transform: scale(1.2, 1.2) rotate(45deg);
		        transform: scale(1.2, 1.2) rotate(45deg);
	}
	25% {
		-webkit-transform: scale(1, 1) rotate(90deg);
		        transform: scale(1, 1) rotate(90deg);
	}
	37.5% {
		-webkit-transform: scale(0.8, 0.8) rotate(135deg);
		        transform: scale(0.8, 0.8) rotate(135deg);
	}
	50% {
		-webkit-transform: scale(1, 1) rotate(180deg);
		        transform: scale(1, 1) rotate(180deg);
	}
	62.5% {
		-webkit-transform: scale(1.2, 1.2) rotate(225deg);
		        transform: scale(1.2, 1.2) rotate(225deg);
	}
	75% {
		-webkit-transform: scale(1, 1) rotate(270deg);
		        transform: scale(1, 1) rotate(270deg);
	}
	87.5% {
		-webkit-transform: scale(0.8, 0.8) rotate(315deg);
		        transform: scale(0.8, 0.8) rotate(315deg);
	}
	100% {
		-webkit-transform: scale(1, 1) rotate(360deg);
		        transform: scale(1, 1) rotate(360deg);
	}
}

@keyframes blazeit {
	0% {
		-webkit-transform: scale(1, 1) rotate(0deg);
		        transform: scale(1, 1) rotate(0deg);
	}
	12.5% {
		-webkit-transform: scale(1.2, 1.2) rotate(45deg);
		        transform: scale(1.2, 1.2) rotate(45deg);
	}
	25% {
		-webkit-transform: scale(1, 1) rotate(90deg);
